.about-me {
    padding: 10em 0;

    .img-wrapper {
        img {
            width: 100%;
            box-shadow: 0 25px 65px rgba(0, 0, 0, .1);
            border-radius: 8px;
        }
    }

    .info-wrapper {
        color: #fff;
        font-family: 'Poppins', sans-serif;
        height: 100%;
        display: flex;
        justify-content: space-between;
        flex-flow: column;

        .info-header {
            h2 {
                font-size: 3.75rem;
                font-weight: 700;
                margin-bottom: 10px;
            }

            p {
                font-size: 1.2rem;
                color: rgba(198, 201, 216, .75);
                font-weight: 300;
                font-size: 18px;
                line-height: 30px;
            }
        }
    }

    .info-nav {
        list-style: none;
        margin-bottom: 25px;
        border-bottom: none;

        a {
            transition: all .3s ease;
            border: none;
        }

        a.selected {
            padding-left: 20px;
            color: #f9004d;
        }

        li {
            display: inline-block;
            margin-right: 20px;
            padding-bottom: 7px;
            position: relative;
            font-size: 1.2rem;
            cursor: pointer;

            &::after {
                content: '';
                transition: all .3s ease-in-out;
                width: 35%;
                height: 2px;
                position: absolute;
                bottom: 0;
                left: 0;
                background: rgba(198, 201, 216, .75);
                -webkit-transition: all .3s ease-in-out;
                -moz-transition: all .3s ease-in-out;
                -ms-transition: all .3s ease-in-out;
                -o-transition: all .3s ease-in-out;
            }

            &.selected {
                color: #f9004d;
            }

            &.selected::after {
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                background-color: #f9004d;
                height: 2px;
            }

            &:hover::after {
                width: 100%;
            }
        }
    }

    .details-list {
        list-style: none;

        li {
            margin-bottom: 20px;
            color: rgba(198, 201, 216, .75);
        }
    }
}

.tabs-section {
    position: relative;

    .tab-content {
        height: auto;
        min-height: 200px;
        justify-content: start;

        .details-list {
            margin-top: 60px;
        }
    }
}

.the-info {
    min-height: 335px;
}

.info-nav {
    flex-flow: row;
    border: none;
    justify-content: start;

    .nav-link {
        padding-bottom: 7px;
        margin-right: 20px;
        position: relative;
        font-size: 1.2rem;
        cursor: pointer;
        color: #fff;
        padding-left: 0;

        &:hover {
            border: none;
        }

        &::after {
            content: '';
            transition: all .3s ease-in-out;
            width: 35%;
            height: 2px;
            position: absolute;
            bottom: 0;
            left: 0;
            background: rgba(198, 201, 216, .75);
            -webkit-transition: all .3s ease-in-out;
            -moz-transition: all .3s ease-in-out;
            -ms-transition: all .3s ease-in-out;
            -o-transition: all .3s ease-in-out;
        }

        &.selected {
            color: #f9004d;
            background-color: transparent;
            border: none;
        }

        &:hover::after {
            width: 50%;
        }

        &.selected::after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            background-color: #f9004d;
            height: 2px;
        }

    }
}

@media(max-width: 768px) {
    .info-nav .nav-link {
        padding: 0;
        font-size: 1.1rem;
        margin-bottom: 7px;
    }
}